'use client';
import { useState } from 'react';
import Image from 'next/image';
import Logo from '@/assets/img/S.png';
import Pencil from '@/assets/svg/pencil.svg';
import Link from 'next/link';
import AnimatedLink from './AnimatedLink';
import { AnimatePresence, motion } from 'framer-motion';

const navLinks = [
  { title: 'What we do', href: '/' },
  { title: 'How it works', href: '/' },
  { title: 'Case studies', href: '/' },
  { title: 'About', href: '/' },
  { title: 'Contact', href: '/' },
];

const menuVars = {
  initial: { scaleY: 0 },
  animate: {
    scaleY: 1,
    transition: { duration: 0.5, ease: [0.12, 0, 0.39, 0] },
  },
  exit: {
    scaleY: 0,
    transition: { delay: 0.5, duration: 0.5, ease: [0.22, 1, 0.36, 1] },
  },
};

const containerVars = {
  initial: {
    transition: {
      staggerChildren: 0.09,
      staggerDirection: -1,
    },
  },
  open: {
    transition: {
      delayChildren: 0.3,
      staggerChildren: 0.09,
      staggerDirection: 1,
    },
  },
};

const mobileLinkVars = {
  initial: {
    y: '30vh',
    transition: { duration: 0.5, ease: [0.37, 0, 0.63, 1] },
  },
  open: {
    y: 0,
    transition: { ease: [0, 0.55, 0.45, 1], duration: 0.7 },
  },
};

const MobileNavLink = ({ title, href }) => (
  <motion.div variants={mobileLinkVars} className="text-5xl uppercase text-black">
    <Link href={href}>{title}</Link>
  </motion.div>
);

const Navbar = () => {
  const [open, setOpen] = useState(false);
  const toggleMenu = () => setOpen((prevOpen) => !prevOpen);

  return (
    <header>
      <nav className="flex justify-between items-center py-8 lg:py-4 px-2 text-white h-24">
        <div className="flex items-center gap-[1ch]">
          <Image src={Logo} width={70} height={70} alt="logo" />
          <span className="text-sm font-semibold tracking-widest">PORTFOLIO</span>
        </div>
        <div className="lg:flex hidden gap-12 text-md text-zinc-400">
          {navLinks.map((link) => (
            <Link key={link.title} href={link.href}>
              <AnimatedLink title={link.title} />
            </Link>
          ))}
        </div>
        <div className="lg:hidden cursor-pointer text-md text-black" onClick={toggleMenu}>
          Menu
        </div>
      </nav>
      <AnimatePresence>
        {open && (
          <motion.div
            variants={menuVars}
            initial="initial"
            animate="animate"
            exit="exit"
            className="fixed left-0 top-0 w-full h-screen origin-top bg-yellow-400 text-black p-10"
          >
            <div className="flex h-full flex-col">
              <div className="flex justify-between">
                <h1 className="text-lg text-black">Portfolio</h1>
                <p className="cursor-pointer text-md text-black" onClick={toggleMenu}>
                  Close
                </p>
              </div>
              <motion.div
                variants={containerVars}
                initial="initial"
                animate="open"
                exit="initial"
                className="flex flex-col h-full justify-center font-lora items-center gap-4"
              >
                {navLinks.map((link) => (
                  <div className="overflow-hidden" key={link.title}>
                    <MobileNavLink title={link.title} href={link.href} />
                  </div>
                ))}
              </motion.div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </header>
  );
};

export default Navbar;
